import React, { Component } from 'react';
import Airtable from 'airtable';
import { ClipLoader } from 'react-spinners';
import styles from './Input.module.css';
import { sendAmplitudeData } from '../../utilities/amplitude.js';

function validateEmail(email) {
    var re = /\S+@\S+\.\S+/;
    return re.test(email);
}

function getReferral(url) {
    const sections = url.split('?ref=')
    return sections.length > 1 ? parseInt(sections[1]) : 0;
}

class Input extends Component {
    constructor(props) {
        super(props);

        this.state = {
            email: '',
            error: false,
            loading: false,
            base: new Airtable({apiKey: 'keyKtcMqi7HtU4xeE'}).base('app1J2mAjmEcYQhIe'),
        }

        this.handleKeyDown = this.handleKeyDown.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onClick = this.onClick.bind(this);
    }

    handleKeyDown(e) {
        if (e.key === 'Enter') {
            this.onClick()
        }
    }

    onChange(e) {
        this.setState({ email: e.target.value })
    }

    onClick() {
        if (!validateEmail(this.state.email)) {
            return this.setState({ error: true });
        }

        const id = Date.now();

        this.setState({
            error: false,
            loading: true,
        })

        this.state.base('Users').create({
          email: this.state.email,
          id,
          referral_id: getReferral(window.location.href),
        }, (err, record) => {
            if (err) { console.error(err); return; }
            
            this.setState({ loading: false })
            this.props.openModal(id);
            sendAmplitudeData('Submit email', { id });
        });
    }

    render() {
        return (
            <div className={styles.root}>
                <input
                    value={this.state.value}
                    onChange={this.onChange}
                    onKeyDown={this.handleKeyDown}
                    className={styles.input}
                    placeholder="Enter work email"
                />
                <button onClick={this.onClick} className={styles.button}>
                    {this.state.loading ? (
                        <ClipLoader
                            sizeUnit={"px"}
                            size={10}
                            color={'white'}
                            loading={this.state.loading}
                        />
                    ) : "Try it free"}
                </button>
                {this.state.error && <span className={styles.error}>Invalid email</span>}
            </div>
        );
    }
}

export default Input;