import React, { Component } from 'react';
import styles from './Modal.module.css';
import { sendAmplitudeData } from '../../utilities/amplitude.js';

class Modal extends Component {
    constructor(props) {
        super(props);

        this.onClick = this.onClick.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    onClick() {
        sendAmplitudeData('Select referral link');
    }

    closeModal(e) {
        if (e.target === e.currentTarget) {
            sendAmplitudeData('Close modal');
            this.props.closeModal()
        }
    }

    render() {
        return (
            <div className={styles.root} onClick={this.closeModal}>
              <div className={styles.modal}>
                <div className={styles.header}>Thanks for your interest!</div>
                <div className={styles.subHeader}>Our team will reach out to you shortly.</div>
              </div>
            </div>
        );
    }
}

export default Modal;