import React, { Component } from 'react';
import Drift from 'react-driftjs';
import styles from './Home.module.css';
import Logo from '../../components/Logo/Logo.js';
import TextContainer from '../../components/TextContainer/TextContainer.js';
import Calendar from '../../components/Calendar/Calendar.js';
import Modal from '../../components/Modal/Modal.js';
import Feature from '../../components/Feature/Feature.js';
import { sendAmplitudeData } from '../../utilities/amplitude.js';
import Collect from '../../images/illustrations/Collect.png';
import Measure from '../../images/illustrations/Measure.png';
import Collaborate from '../../images/illustrations/Collaborate.png';
import Input from '../../components/Input/Input.js';
import Footer from '../../components/Footer/Footer.js';
import Dashboard from '../../images/illustrations/Dashboard.png';

class Home extends Component {
    constructor(props) {
        super(props);

        this.state = {
            modal: false,
            id: null,
            isIntersecting: false,
            offset: 0,
            showPress: false,
        }

        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    componentDidMount() {
        sendAmplitudeData('Visit website');
    }

    openModal(id) {
        this.setState({
            modal: true,
            id,
        });
    }

    closeModal() {
        this.setState({ modal: false });
    }

    render() {
        return (
            <div className={styles.root}>
                {this.state.modal && <Modal closeModal={this.closeModal} id={this.state.id} />}
                <div className={styles.main}>
                    <div className={styles.header}>
                    <Logo />
                    </div>
                    <div className={styles.row}>
                        <div className={styles.left}>
                            <TextContainer openModal={this.openModal} />
                        </div>
                        <div className={styles.right}>
                            <Calendar />
                        </div>
                    </div>
                </div>
                <div className={styles.dashboard}>
                    <div className={styles.mainText}>The ad platform for sponsors</div>
                    <div className={styles.subText}>Automatically calculate valuable metrics such as impressions, CTR, and CPM.</div>
                    <img alt="" src={Dashboard} />
                </div>
                <Feature
                    image={Collect}
                    alt="Collect"
                    title="Collect"
                    text={[`Easily deploy our analytics tools across various platforms including Twitch, Twitter, tournaments, and more`]}
                />
                <Feature
                    image={Collaborate}
                    alignRight
                    alt="Collaborate"
                    title="Collaborate"
                    text={[
                        'Team up with teams and influencers',
                        'Discover which activations are performant'
                    ]}
                />
                <Feature
                    image={Measure}
                    alt="Measure"
                    title="Collaborate with Sponsors"
                    text={[
                        'Price activations with AI precision',
                        'Focus on strategy, not on spreadsheets',
                        'Get esports fans to love your brand',
                    ]}
                />
                <div className={styles.callToAction}>
                    <div className={styles.title}>
                        Get in touch
                    </div>
                    <div className={styles.inputContainer}>
                        <Input openModal={(id) => this.openModal(id)} />
                    </div>
                </div>
                <Footer togglePress={this.togglePress} />
                <Drift appId="86zgpr8t4h6w" />
            </div>
        );
    }
}

export default Home;
